import React, { useEffect, useState } from 'react';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import GameScreen from './GameScreen';
import StatsScreen from './StatsScreen';
import StartScreen from './StartScreen';
import RulesScreen from './RulesScreen';

const Stack = createNativeStackNavigator();

export default function App() {
    return (
        <NavigationContainer>
            <Stack.Navigator initialRouteName="Start" screenOptions={{ headerShown: false }}>
                <Stack.Screen name="Game" component={GameScreen}></Stack.Screen>
                <Stack.Screen name="Stats" component={StatsScreen}></Stack.Screen>
                <Stack.Screen name="Rules" component={RulesScreen}></Stack.Screen>
                <Stack.Screen name="Start" component={StartScreen}></Stack.Screen>
            </Stack.Navigator>
        </NavigationContainer>
    )
}