import React, { useEffect, useState } from 'react';
import { Ionicons } from '@expo/vector-icons';
import { SafeAreaView, View, Text, StyleSheet, Pressable, Dimensions, Platform } from 'react-native';
import HangmanFigure from './HangmanFigure';

const polWords = ['SAMOCH√ìD', 'KOMPUTER', 'JAB≈ÅKO', 'SZKO≈ÅA', 'PRZYJACIEL', 'PI≈ÅKA', 'WAKACJE'];
const alphabet = 'AƒÑBCƒÜDEƒòFGHIJKL≈ÅMN≈ÉO√ìPRS≈öTUVWXYZ≈π≈ª'.split('');
const MAX_MISTAKES = 6;

export default function GameScreen({ navigation }) {
    const [word, setWord] = useState('');
    const [guessedLetters, setGuessedLetters] = useState([]);
    const [status, setStatus] = useState('playing');
    const [wins, setWins] = useState(0);
    const [losses, setLosses] = useState(0);

    useEffect(() => {
        startNewGame();
    }, []);

    useEffect(() => {
        if (!word) return;

        const mistakes = guessedLetters.filter(l => !word.includes(l)).length;
        const hasWon = word.split('').every(letter => guessedLetters.includes(letter));
        const hasLost = mistakes >= MAX_MISTAKES;

        if (hasWon) {
            setStatus('won');
            setWins(prev => prev + 1);
        } else if (hasLost) {
            setStatus('lost');
            setLosses(prev => prev + 1);
        }
    }, [guessedLetters]);

    const startNewGame = () => {
        const randomWord = polWords[Math.floor(Math.random() * polWords.length)];
        setWord(randomWord);
        setGuessedLetters([]);
        setStatus('playing');
    };

    const handleLetterPress = (letter) => {
        if (status !== 'playing' || guessedLetters.includes(letter)) return;
        setGuessedLetters(prev => [...prev, letter]);
    };

    const renderWord = () => {
        return word.split('').map((letter, index) => (
            <View key={index} style={styles.letterBox}>
                <Text style={styles.letter}>{guessedLetters.includes(letter) ? letter : ''}</Text>
            </View>
        ));
    };

    const renderKeyboard = () => {
        return (
            <View style={styles.keyboard}>
                {alphabet.map((letter) => (
                    <Pressable key={letter} onPress={() => handleLetterPress(letter)} style={[styles.key, guessedLetters.includes(letter) && styles.keyDisabled]} disabled={guessedLetters.includes(letter) || status !== 'playing'}>
                        <Text style={styles.keyText}>{letter}</Text>
                    </Pressable>
                ))}
            </View>
        );
    };

    const mistakesCount = guessedLetters.filter(l => !word.includes(l)).length;

    return (
        <SafeAreaView style={styles.container}>
            <View style={styles.figureContainer}>
                <HangmanFigure step={mistakesCount}></HangmanFigure>
            </View>

            <View style={styles.wordContainer}>{renderWord()}</View>

            {status !== 'playing' && (
                <Text style={[styles.statusText, status === 'won' ? styles.won : styles.lost]}>
                    {status === 'won' ? 'Wygra≈Çe≈õ!' : 'Przegra≈Çe≈õ!'} S≈Çowo: {word}
                </Text>
            )}

            {renderKeyboard()}

            {status !== 'playing' && (
                <Pressable style={styles.button} onPress={startNewGame}>
                    <Text style={styles.buttonText}>Zagraj ponownie</Text>
                </Pressable>
            )}

            <Pressable style={styles.statsButton} onPress={() => navigation.navigate('Stats', { wins, losses })}>
                <Text style={styles.statsButtonText}>üìä</Text>
            </Pressable>
            <Pressable style={{ position: 'absolute', top: 40, left: 20, zIndex: 10 }} onPress={() => navigation.goBack()}>
                <Ionicons name="arrow-back" size={28} color="black" />
            </Pressable>
        </SafeAreaView>
    );
}

const screenWidth = Dimensions.get('window').width;

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: Platform.OS === 'ios' ? 80 : 60,
        paddingHorizontal: 20,
        backgroundColor: '#fff',
        justifyContent: 'center',
        alignItems: 'center',
    },

    figureContainer: {
        alignItems: 'center',
        marginBottom: 20,
    },
    wordContainer: {
        flexDirection: 'row',
        justifyContent: 'center',
        marginVertical: 30,
        flexWrap: 'wrap',
    },
    letterBox: {
        borderBottomWidth: 2,
        borderColor: '#333',
        width: 30,
        height: 40,
        marginHorizontal: 3,
        alignItems: 'center',
        justifyContent: 'center',
    },
    letter: {
        fontSize: 24,
        fontWeight: 'bold',
    },
    keyboard: {
        flexDirection: 'row',
        flexWrap: 'wrap',
        justifyContent: 'center',
        maxWidth: screenWidth - 20,
    },
    key: {
        width: 35,
        height: 40,
        margin: 4,
        backgroundColor: '#eee',
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: 4,
    },
    keyDisabled: {
        backgroundColor: '#ccc',
    },
    keyText: {
        fontSize: 18,
        fontWeight: 'bold',
    },
    statusText: {
        fontSize: 20,
        marginVertical: 10,
    },
    won: {
        color: 'green',
    },
    lost: {
        color: 'red',
    },
    button: {
        marginTop: 15,
        paddingVertical: 10,
        paddingHorizontal: 20,
        backgroundColor: '#007AFF',
        borderRadius: 6,
    },
    buttonText: {
        color: 'white',
        fontSize: 16,
    },
    statsButton: {
        position: 'absolute',
        top: Platform.OS === 'ios' ? 50 : 30,
        right: 20,
        backgroundColor: '#373F51',
        paddingVertical: 8,
        paddingHorizontal: 12,
        borderRadius: 7,
        zIndex: 10,
    },
    statsButtonText: {
        color: 'white',
        fontSize: 18,
        fontWeight: 'bold',
    },
});