import React from 'react';
import { Dimensions } from 'react-native';
import Svg, { Line, Circle } from 'react-native-svg';

const HangmanFigure = ({ step }) => {
  const screenWidth = Dimensions.get('window').width;
  const size = Math.min(screenWidth - 40, 300);
  const unit = size / 10;

  return (
    <Svg height={size} width={size}>

      <Line x1={unit} y1={unit * 9} x2={unit * 5} y2={unit * 9} stroke="black" strokeWidth="2" />
      <Line x1={unit * 3} y1={unit * 9} x2={unit * 3} y2={unit} stroke="black" strokeWidth="2" />
      <Line x1={unit * 3} y1={unit} x2={unit * 7} y2={unit} stroke="black" strokeWidth="2" />
      <Line x1={unit * 7} y1={unit} x2={unit * 7} y2={unit * 2} stroke="black" strokeWidth="2" />

      {step > 0 && <Circle cx={unit * 7} cy={unit * 3} r={unit} stroke="black" strokeWidth="2" fill="none" />}

      {step > 1 && <Line x1={unit * 7} y1={unit * 4} x2={unit * 7} y2={unit * 6} stroke="black" strokeWidth="2" />}

      {step > 2 && <Line x1={unit * 7} y1={unit * 4.5} x2={unit * 6} y2={unit * 5.5} stroke="black" strokeWidth="2" />}

      {step > 3 && <Line x1={unit * 7} y1={unit * 4.5} x2={unit * 8} y2={unit * 5.5} stroke="black" strokeWidth="2" />}

      {step > 4 && <Line x1={unit * 7} y1={unit * 6} x2={unit * 6} y2={unit * 7.5} stroke="black" strokeWidth="2" />}

      {step > 5 && <Line x1={unit * 7} y1={unit * 6} x2={unit * 8} y2={unit * 7.5} stroke="black" strokeWidth="2" />}
    </Svg>
  );
};

export default HangmanFigure;
