import React from 'react';
import { View, Text, StyleSheet, Pressable } from 'react-native';

export default function StatsScreen({ navigation, route }) {
    const { wins, losses } = route.params;

    return (
        <View style={styles.container}>
            <Text style={styles.header}>Statystyki</Text>
            <Text style={styles.stat}>Wygrane: {wins}</Text>
            <Text style={styles.stat}>Przegrane: {losses}</Text>

            <Pressable style={styles.button} onPress={() => navigation.goBack()}>
                <Text style={styles.buttonText}>Powr√≥t</Text>
            </Pressable>
        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 80,
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#fff',
    },
    header: {
        fontSize: 26,
        fontWeight: 'bold',
        marginBottom: 40,
    },
    stat: {
        fontSize: 20,
        marginVertical: 10,
    },
    button: {
        marginTop: 30,
        backgroundColor: '#373F51',
        paddingHorizontal: 20,
        paddingVertical: 10,
        borderRadius: 6,
    },
    buttonText: {
        color: '#fff',
        fontSize: 16,
    },
});