import React from 'react';
import { View, Text, StyleSheet, Pressable } from 'react-native';

export default function StartScreen({ navigation }) {

    return (
        <View style={styles.container}>
            <Pressable style={styles.button} onPress={() => navigation.navigate('Game')}>
                <Text style={styles.buttonText}>Gra</Text>
            </Pressable>
            <Pressable style={styles.button} onPress={() => navigation.navigate('Rules')}>
                <Text style={styles.buttonText}>Zasady</Text>
            </Pressable>
        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        paddingTop: 80,
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#fff',
    },
    button: {
        marginTop: 30,
        backgroundColor: '#373F51',
        paddingHorizontal: 20,
        paddingVertical: 10,
        borderRadius: 6,
    },
    buttonText: {
        color: '#fff',
        fontSize: 16,
    },
});